body{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    height: auto;
}
ul li{
    list-style-type: none;
}
a{

    text-decoration: none;
    &:link, &:visited{
        color: black;
    }
}
#root{
    width: 100%;
}
.container{
    margin: 0 auto 0 auto;
    width: 75%;
}
.fa-commenting{
    color: darken(rgba(0, 191, 255, .5), 10%);
}
.navBar{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    border-bottom: .25px solid darken(rgba(0, 191, 255, .15), 35%);
    background: white;
    z-index: 1000;
    .navBar--container{
        width: 55%;
        height: 75px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 15px;
        margin: 0 auto;
        input{
            outline: none;
            border: transparent;
            border-radius: 20px;
            background: darken(rgba(0, 191, 255, .25), 30%);
            height: 25px;
            text-align: center;
            color: #000;
            letter-spacing: 1px;
           }
        button{
            background: transparent;
            border: .15px solid deepskyblue;
            border-radius: 20px;
            padding: 5px 10px;
            outline: none;
            margin: 0 5px;
            &:hover{
                cursor: pointer;
            }
        }
    }
}
.navBar--h1{
    font-size: 2rem;
}
.navBar--h1__span{
    font-weight: 300;
    font-size: .9rem;
    display: block;
    color: darken(rgba(0, 191, 255, .5), 35%);
}
.navLinks{
    display: flex;
    align-items: center;
    justify-content: space-around;
    li{
        padding: 10px;
        border: .15px solid deepskyblue;
        border-radius: 20px;
        padding: 5px 10px;
        margin: 0 10px;
    }
}
.hide{
    display: none;
}
.newsFeed{
    position: relative;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: white;
    margin: 70px auto 0 auto;
    ul{
        width: 55%;
        padding: 0;
    }
}
.newsFeed--article-section{
    width: 75%;
}
.loginBox{
    height: 100%;
    z-index: 1000;
    width: 25%;
    align-self: flex-start;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;

}
.newsFeed--newsFilterBar{
    width: 100%;
    text-align: center;
    background: white;
}
.newsFeed--newsFilterBar--container{
    padding-top: 10px;
    margin: 0;

}
.newsFeed--newsFilterBar--header{
    width: 85%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
}
.newsFeed--newsFilterBar--buttons{
    display: flex;
    align-items: center;
    ul{
        margin: 25px auto;
        li{
            padding: 15px;
            margin: -15px;
            border-bottom: 3px solid transparent;
            background: lighten(skyblue, 100%);
            transition: all .35s ease-in-out;
            &:hover{
                cursor: pointer;
                border-bottom: 3px solid lighten(skyblue, 10%);;
                background: lighten(skyblue, 25%);
            }
        }
    }
    ul{
        width: 95%;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
    }
}
.newsFeed--newsFilterBar--image{
    position: relative;
    height: 405px;
    .newsFeed--newsFilterBar--image__overlay{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        background: linear-gradient(to left bottom, rgba(0, 0, 0, .35), rgba(0, 0, 0, .75));
        .info-box{
            width: 90%;
            word-break: break-all;
            color: white;
            text-align: left;
            padding: 0;
            margin-bottom: 0;
            position: absolute;
            bottom: 0;
            left: 0;
            padding: 0 0 15px 5px;
            font-size: 1.25rem;
            p{
                margin: 0;
                padding: 0 0 0 5px;
            }
            p:last-of-type{
                padding: 5px;
            }
        }
    }
    img{
        height: 100%;
        width: 100%;
    }
}
.newsFeed--article--container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: auto;
    background: white;
    margin: 25px 0;
    border-bottom: .25px solid rgba(0, 0, 0, .25);
    &:first-of-type{
        margin-top: 10px;

    }
}
.newsFeed--article--container--info{
    width: 100%;
    .button-container{
        display: flex;
        button{
            margin: 0 10px;
            border: none;
            background: transparent;
            padding: 5px 10px;
            display: flex;
            i{
                font-size: 1.25rem;
            }
            &:hover{
                cursor: pointer;
            }

        }
    }

}
.newsFeed--article--title{
    text-align: left;
    font-size: 1rem;
    font-weight: 500;
    width: 90%;
}
.newsfeed--article--image{
width: 25%;
display: flex;
align-items: flex-end;
justify-content: flex-end;
padding-bottom: 15px;
margin-bottom: 15px;
    a{
        display: flex;
        align-items: center;
        width: 100%;
        height: 100px;
        flex-direction: column;
        img{
            width: 100%;
            margin: 0 auto;
            height: 100%;
        }
    }
}

.login--form{
    border: 0.25px solid rgba(0, 57, 77, 0.15);
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 250px;
    margin-left: 25px;
    position: fixed;
    top: 100px;
    background: url('https://images.pexels.com/photos/1022923/pexels-photo-1022923.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260');
    background-size: cover;
    background-position: bottom;
    i{
        font-size: 6rem;
        color: deepskyblue;
    }
    form{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        input{
            margin: 5px 0;
        }
    }
}
.divider{
    margin: 10px 0;
    width: 100%;
    text-align: center;
}
.loginButton{
    background: transparent;
    border: 2px solid deepskyblue;
    border-radius: 20px;
    padding: 5px 10px;
}
.loginFooter{
    position: absolute;
    bottom: -20px;
    font-size: .87rem;
&:last-of-type{
    bottom: -40px;
}
}

.article-image-missing{
    font-size: 4.5rem;
}